@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Global Styling */
* {
    margin: 0;
    padding: 0;
}

html {
    overflow-y: scroll;
    /* This code stops the page from moving when clicking on a new link */
}

.active a {
    color: #fff;
}

body {
    background-color: #d1dbe6;
}

.card-body {
    background-color: #6DAFFE;
    border-radius: 5px;
    border-color: 1px solid #fff;
}

h1 {
    color: #fff;
    padding-bottom: 20px;
    font-weight: bold;
    line-height: 50px;
    border-bottom: 3px solid #fff;
    font-family: 'Montserrat', sans-serif;
}

#sectionBody {
    padding-top: 55px;
    padding-bottom: 55px;
}

a:hover {
    text-decoration: none;
}

@media (min-width: 1200px) {
    .container {
        justify-content: center;
        display: flex;
    }
}




#navbarMainStyling {
    background: #437FC7;
    border-bottom: 8px solid #6DAFFE;
}

#brandNameStyling {
    font-weight: bold;
    color: #fff;
    font-size: 25px;
    font-family: 'Montserrat', sans-serif;
}

.nav-item {
    font-weight: 500;
}

.navbar-light .navbar-nav .nav-link {
    color: #fff;
}

.navbar-light .navbar-nav .active>.nav-link,
.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .nav-link.show,
.navbar-light .navbar-nav .show>.nav-link {
    color: #fff;
}

.navbar-light .navbar-toggler {
    color: #fff;
    border-color: #fff;
}

.navbar-light .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255,255,255, 0.9)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}
/* Footer Styling */

#footerSection {
    padding-top: 10px;
    font-size: 20px;
    color: #fff;
    text-align: center;
    background: #437FC7;
    border-top: 8px solid #6DAFFE;
}

.socialMedia {
    font-size: 50px;
}

.fa-github {
    color:#fff;
}

.fa-linkedin-square{
    color:#fff;
}
/* Index Page Styling */

#profilePicture {
    padding-right: 20px;
}

#indexBodyContent {
    padding-top: 10px;
}

p {
    color: #fff;
    font-size: 17px;
}

.informationBox {
    text-align: center;
    border-radius: 5px;
    border: 1px solid #fff;
    background-color: #437FC7;
    padding-top: 15px;
}
/* Contact Page Styling */

.form-group {
    width: 80%;
    margin-left: 10px;
    font-weight: 500;
}

.form-text {
    color: #fff;
}

#submitButton {
    background-color: #437FC7;
    color: #fff;
    border-radius: 5px;
    border-color: #fff;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 18px;
    padding-left: 18px;
    font-weight: 600;
    font-size: 20px;
}
/* Portfolio Styling */

.portfolioImg {
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 5px;
    border: 2px solid #437FC7;
    text-align: center;
    font-size: 300px;
    color: #6DAFFE;
    background-color: #EDF6FF;
}

/* Extra small devices (Tested with Iphone X) */
@media (min-width: 375px) { .portfolioImg { width: 340px; height: 300px;} }

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) { .portfolioImg { width: 500px; height: 350px;} }

 /* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) { .portfolioImg { width: 650px; height: 400px; } }

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) { .portfolioImg { width: 800px; height: 500px; } }

/* // Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) { .portfolioImg { width: 1000px; height: 600px; } }



.portfolio-text {
    text-align: center;
    color: #fff;
    text-decoration: underline;
    -webkit-text-decoration-color: #fff;
    text-decoration-color: #fff;
}

.repo {
    text-align: center;
    border-radius: 5px;
    border: 2px solid #437FC7;
    background-color: #437FC7;
}

.repoLink {
    color: #fff;
    border: 1px solid #6DAFFE;
    border-radius: 5px;
}

.slick-next,
.slick-prev {
    font-size: 0;
    line-height: 0;
    position: absolute;
    top: 50%;
    display: block;
    width: 20px;
    height: 20px;
    padding: 0;
    transform: translate(0, -50%);
    cursor: pointer;
    color: #fff;
    border: none;
    outline: 0;
    background: 0 0;
}

.slick-prev:before,
.slick-next:before {
    color: #fff;
}

.slick-dots li button:before {
    color: #fff;
}

.slick-prev:before,
.slick-next:before {
    color: #fff;
}

.slick-dots li button:before {
    color: #fff;
}

.slick-dots li.slick-active button:before {
    opacity: .75;
    color: #fff;
}
